* {
  margin: 0px
}
:root {
  --black: #18191A;
  --lightBlack: #242526;
  --purpleBlack: #272531;
  --green:#2aa44f;
  --greenHover: #3fbd64;
  --greyBlack:#3F414A;
  --hoverGrey:#4e505a;
  --border: #464646;
  --palletes:#3A3B3C;
  --boldText: #E4E6EB;
  --icon:#b0b3b8;
  --secondaryText: #b0b3b8;
  --textRed: #f85149;
  --boxShadow: rgba(37, 37, 37, 0.3) 0px 1px 2px 0px,
  rgba(0, 0, 0, 0.15) 0px 2px 6px 2px;
  --primaryFontSize:.9375rem;
  --purpleGradient: linear-gradient(46.77deg, #0066FF 15.68%, rgba(169, 85, 183, 0.911342) 70.66%);
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.MuiPaper-root {
  color: #E4E6EB !important;
  color: var(--boldText) !important;
  font-family: Arial, Helvetica, sans-serif;
  background-color: #242526 !important;
  background-color: var(--lightBlack) !important;
}
html {
  scrollbar-face-color: #646464;
  scrollbar-base-color: #646464;
  scrollbar-3dlight-color: #646464;
  scrollbar-highlight-color: #646464;
  scrollbar-track-color: #000;
  scrollbar-arrow-color: #000;
  scrollbar-shadow-color: #646464;
  scrollbar-dark-shadow-color: #646464;
}

::-webkit-scrollbar {
  width: 8px;
  height: 3px;
}

/* ::-webkit-scrollbar-button {
  background-color: #666;
} */

::-webkit-scrollbar-track {
  background-color: #646464;
}

::-webkit-scrollbar-track-piece {
  background-color: #000;
}

::-webkit-scrollbar-thumb {
  height: 50px;
  background-color: #666;
  border-radius: 3px;
}

::-webkit-scrollbar-corner {
  background-color: #646464;
}

::-webkit-resizer {
  background-color: #666;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
body {

    background-color: var(--black);
    overflow-y: scroll;
}
.MuiDialog-container{
    overflow-y: scroll;
}

.peopleMatchCard__content__buttons {
    display: flex;
    justify-content: space-around;
}

