    height: 30px;
    /* background: rgb(226, 226, 226); */
    border-radius: 6px;
    /* color: rgb(75, 75, 75); */
    color: #499E4C;
    transition: 200ms;
    cursor: pointer;
    color: whitesmoke;
    background: var(--green);
}

.peopleMatchCard__content__button:hover>button {

    background: var(--greenHover);


}

