    display: flex;
    justify-content: center;
}

.postIconButtons--ticketButtons>* {
    width: 100%;
   display: flex;
   justify-content: center;
   align-items: center;
}
.postIconButtons--BookNow {
    color: #499E4C;
    display: flex;
    align-items: center;
    cursor: pointer;
    border-radius: 10px;
    background-color: var(--palletes);
    padding: 0 20px 0 10px;
}
.postIconButtons--BookNow:hover{
    background-color: #444444;
}
.postIconButtons--BookNow:active{
    background-color: #333333;
}
.postIconButtons--CancelBooking{
     color:grey;
     display: flex;
     align-items: center;
     cursor: pointer;
     border-radius: 10px;
     background-color:var(--palletes);
     padding: 0 20px 0 10px;
}
.postIconButtons--CancelBooking:hover {
    background-color: #444444;
}
.postIconButtons--CancelBooking:active {
    background-color: #333333;
}
.postIconButtons--schedule {
    display: flex;
    align-items: center;
    font-weight: lighter;
    color: lightgray;
}

.postIconButtons--BookNow>.MuiButtonBase-root>.MuiIconButton-label>.MuiSvgIcon-root {
    color: #499E4C;
    font-size: 30px !important;
}
.postIconButtons--CancelBooking>.MuiButtonBase-root>.MuiIconButton-label>.MuiSvgIcon-root {
    color: grey;
    font-size: 30px !important;
}
.postIconButtons--schedule>.MuiButtonBase-root>.MuiIconButton-label>.MuiSvgIcon-root,
.postIconButtons--share>.MuiButtonBase-root>.MuiIconButton-label>.MuiSvgIcon-root,
.postIconButtons--likeButton>.MuiButtonBase-root>.MuiIconButton-label>.MuiSvgIcon-root,
.postIconButtons--comment>.MuiButtonBase-root>.MuiIconButton-label>.MuiSvgIcon-root {
    color: lightgray;
    font-size: 30px;
}
.postIconButtons.ticket {
    justify-content: center;
    margin: 0px 19px 4px;
}
