.moviePoster {
    position: relative;
}

.moviePoster__detailCard {
    opacity: 0;
    height: 100%;
    width: 185px;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(140, 140, 140, 0.450848) 0%, rgba(119, 119, 119, 0.535275) 0.01%, rgba(65, 65, 65, 0.745809) 0.02%, rgba(12, 12, 12, 0.951377) 86.46%, #000000 100%);
    ;
    z-index: 1;
    top: 0;
    left: 0;
    position: absolute;
    border-radius: 10px;
    transition: 450ms;
    display: flex;
    flex-flow: column;
    justify-content: flex-end;
    align-items: center;
}

.moviePoster__detailCard:hover {
    opacity: 1;
}

.moviePoster--image {
    object-fit: contain;
    border-radius: 10px;
    max-width: 200px;
}

.moviePoster__detailCard__info {
    margin-bottom: 10px;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
}

.moviePoster__detailCard__title>span {
    color: white;
    font-weight: 900;
    /* font-size: calc(16px / 1.3); */
}

.moviePoster__detailCard__overview>span {
    color: white;

    font-size: calc(18px / 1.3);
    /* margin-left: 5px; */
}

.moviePoster__detailCard__overview {
    text-align: center;
    margin: 10px 5px 5px 5px;
}

.moviePoster__detailCard__button>.MuiButtonBase-root>.MuiIconButton-label>.MuiSvgIcon-root {
    color: lightgray;
}

.moviePoster__detailCard__buttons {
    display: flex;
    justify-content: space-around;
    transform: translate(0px, 40px);
    visibility: collapse;
    transition: 300ms;
}

.moviePoster__detailCard__button.selected>.MuiButtonBase-root>.MuiIconButton-label>.MuiSvgIcon-root {
    color: purple;
}

.moviePoster__detailCard__title:hover>span {
    text-decoration: underline;
}

/* .moviePoster__detailCard:hover>.moviePoster__detailCard__buttons {
    transform: translate(0px, -5px);
    visibility: visible;
} */

.moviePoster__detailCard__buttons.show {
    transform: translate(0px, -5px);
    visibility: visible;
}
